import store from "@/store";
const onlyAuth = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    //       .
    next("/login");
  } else {
    next();
  }
};

export default [
  {
    meta: {
      title: "Main"
    },
    path: "/",
    name: "",
    beforeEnter: onlyAuth,
    component: () => import("@/views/Main.vue")
  },

  {
    meta: {
      title: "MyPage"
    },
    path: "/mypage",
    name: "",
    beforeEnter: onlyAuth,
    component: () => import("@/views/MyPage.vue")
  },
  {
    meta: {
      title: "ObituaryList"
    },
    path: "/obituary-list",
    name: "",
    beforeEnter: onlyAuth,
    component: () => import("@/views/ObituaryList.vue")
  }
];
