import store from "@/store";
const onlyAuth = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    //       .
    next("/login");
  } else {
    next();
  }
};

export default [
  {
    meta: {
      title: "PayBack"
    },
    path: "/payback",
    name: "PayBack",
    beforeEnter: onlyAuth,
    component: () => import("@/views/payback/PayBack.vue")
  }
];
